@import "../styles/variables.scss";

.home {
  height: 100%;
}

.introduction {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  &__name {
    font-family: "Valencia", serif;
    font-size: 150px;
  }
  &__info {
    font-family: "Avenir", "Helvetica", "Ariel", sans-serif;
    font-size: 50px;
  }
  &__item {
    position: relative;
    z-index: 1;
  }
}

.hide {
  background-color: white;
  overflow-y: clip;
  span {
    display: inline-block;
    transform: translateY(100%);
  }
}

.about {
  height: 50vh;
  width: 100%;
  padding-top: 5%;
  position: relative;
  overflow: hidden;

  &__description {
    align-items: center;
    text-align: left;
    margin-left: 30%;
    margin-right: 5%;

    font-weight: 300;
    font-size: 70px;
  }
}
.about__title,
.skills__title,
.contacts__title {
  transition: transform 0.7s ease-out;
  font-family: "AvenirHeavy", sans-serif;
  position: absolute;
  font-size: 350px;
  color: #eee;
  top: 5%;
  width: 100%;
  text-align: center;
  left: -100%;
  white-space: nowrap;
  z-index: -1;
}
.skills {
  position: relative;
  padding-top: 15%;
  overflow: hidden;
}
.skills__description {
  display: grid;
  padding-top: 10%;
  grid-template-columns: repeat(2, auto);
  font-weight: 300;
  font-size: 70px;
  min-height: 100vh;
  .skills__item {
    font-family: AvenirRoman, sans-serif;
    font-size: 35px;
    justify-self: center;
  }
}
.contacts {
  position: relative;
  overflow: hidden;
  min-height: 100vh;
}
.contacts__title {
  left: 0;
  right: 100%;
}
.contacts__description {
  display: flex;
  justify-content: space-around;
  height: 100%;
  padding-top: 20%;
}
.contacts__link {
  width: 256px;
  height: 256px;
  img {
    width: 100%;
    height: 100%;
  }
}
