@import "../../styles/variables";

.projects {
  display: flex;
  flex-direction: column;
  width: 100%;

  &__item {
    display: flex;
    flex-flow: row nowrap;
    height: 100vh;
    margin-bottom: 200px;
    color: white;
    .projects__item-img-wrapper {
      width: 100%;
      height: 100%;
      overflow: scroll;
    }
    .projects__item-img-wrapper::-webkit-scrollbar {
      display: none;
    }
    &-img {
      width: 100%;
    }
  }
  .swiper-project {
    margin-right: 0;
  }
  .mandatory-scroll-snapping {
    scroll-snap-align: end;
  }
}
